:root {
  --font-family: "Montserrat", sans-serif;
  --content-width: 1440px;
  --container-offset: 0px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --blue-color: #0f33ff;
  --dark-color: #0b0c0c;
  --gray-color: #555;
}

